





























import Vue from 'vue';
import {mapState, mapActions, mapGetters} from 'vuex';
export default Vue.extend({
  computed: {
    ...mapState('eventLogs', ['patients']),
    ...mapGetters('eventLogs', ['viewFriendlyPatients'],)
  },
  methods: {
    ...mapActions('eventLogs', ['loadEvents'],)
  },
  mounted() {
    this.loadEvents();
  }
});
